import { Button, FormControl, FormLabel, Input, InputGroup, InputRightElement, Show, VStack } from '@chakra-ui/react';
import React from 'react';
import {useState} from 'react';
import { useToast } from '@chakra-ui/react';
import axios from 'axios';
import {useHistory} from "react-router-dom"


const Login = () => {
  
    
    const [show,setShow]=  useState(false)
    const [email,setemail]=  useState()
    const [password,setpassword]=  useState()
    const handleClick =()=>setShow(!show);
    const [loading,setLoading]=useState(false);

    const toast = useToast();
    const history=useHistory();

    
          const submitHandler=async()=>{
        setLoading(true);
        if( !email || !password ){
          toast({
          title: "Please Fill all the Feilds!",
          status: "warning",
          duration: 5000,
          isClosable: true,
          position:"bottom",
        });
        setLoading(false);
        return;
        }
        
        try {
            const config={
                headers:{
                    "Content-type":"application/json",
                },
            };
            const {data }=await axios.post("/api/user/login",{email,password},
            config
            );
          toast({
          title: "Login Successful",
          status: "success'",
          duration: 5000,
          isClosable: true,
          position:"bottom",
        });
         SpeechSynthesisUtterance(data);
        localStorage.setItem("userInfo",JSON.stringify(data));
        setLoading(false);
        history.push("/chats");   
        } catch (error) {
          toast({
          title: "Error Occured!",
          description : error.response.data.message,
          status:"error",
          duration: 5000,
          isClosable: true,
          position:"bottom",
        });
        setLoading(false);
    }

       };

       

  return (
    <VStack spacing="5px" color="black">

        <FormControl id='email' isRequired >
            <FormLabel>Email</FormLabel>
            <Input placeholder='Enter Your Email'
              value={email}
            onChange={(e)=>setemail(e.target.value)}
            ></Input>
        </FormControl>

        <FormControl id='password' isRequired >
            <FormLabel>Password</FormLabel>
            <InputGroup>
                <Input
            type={show ? "text" :"password"} placeholder='Enter Your Password'
            value={password}
            onChange={(e)=>setpassword(e.target.value)}
            />
            <InputRightElement width="4.5rem">
                <Button h="1.75rem" size="sm" onClick={handleClick} >
                {
                    show ? "Hide" : "Show"
                }
                </Button>
            </InputRightElement>
            </InputGroup>
          
        </FormControl>

        
        <Button
           colorScheme='blue'
           width="100%"
           style={{marginTop:15}}
           onClick={submitHandler}
           isLoading={loading}
        >LogIn</Button>

          <Button
          variant="solid"
           colorScheme='red'
           width="100%"
           onClick={()=>{
            setemail('guest@example.com');
            setpassword("123456");
           }}
        > Get Guest User </Button>

    </VStack>
  );
  
}

export default Login
